@import "https://fonts.googleapis.com/css2?family=Baloo+2:wght@400;500;600;700&family=Quicksand:wght@300;400;500;600&family=Cormorant+Garamond:ital,wght@0,300;0,400;0,500;0,600;1,300;1,400;1,500;1,600&family=Knewave:wght@400&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #333;
  background: #fff;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.6;
}

.app {
  background: #fff;
  flex-direction: column;
  min-height: 100vh;
  display: flex;
}

.pottery-grid {
  background: #fff;
}

.vase-container {
  background: #fff;
  max-width: 600px;
  margin: 4rem auto 3rem;
  padding: 3rem 4rem 2rem;
  position: relative;
}

.vase-container:before {
  content: "";
  pointer-events: none;
  z-index: 1;
  border: 3px solid #000;
  border-radius: 85% 85% 80% 80% / 35% 35% 85% 85%;
  width: 95%;
  height: 100%;
  position: absolute;
  top: 15%;
  left: 50%;
  transform: translateX(-50%);
}

.vase-container:after {
  content: "";
  pointer-events: none;
  z-index: 3;
  background: #fff;
  border: 3px solid #000;
  border-bottom: none;
  border-radius: 50% 50% 20% 20% / 60% 60% 40% 40%;
  width: 60%;
  height: 25%;
  position: absolute;
  top: -15px;
  left: 50%;
  transform: translateX(-50%);
}

.header {
  text-align: center;
  z-index: 2;
  background: none;
  padding: 0;
  position: relative;
}

.site-title {
  letter-spacing: .05em;
  color: #000;
  text-transform: uppercase;
  margin: 2rem 0 1rem;
  font-family: Knewave;
  font-size: 2.5rem;
  font-weight: 600;
}

.site-subtitle {
  color: #8b7355;
  font-size: 1.2rem;
  font-style: italic;
  font-weight: 300;
}

.navigation {
  justify-content: center;
  gap: 0;
  display: flex;
  position: relative;
}

.navigation:before {
  content: "";
  background: #e0e0e0;
  height: 1px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.navigation:after {
  content: "";
  background: #e0e0e0;
  height: 1px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav-item {
  color: #666;
  cursor: pointer;
  letter-spacing: .02em;
  background: none;
  border: none;
  padding: .8rem 2.5rem;
  font-family: Fredoka, cursive;
  font-size: 1.1rem;
  font-weight: 500;
  transition: all .3s;
  position: relative;
}

.nav-item:hover {
  color: #000;
}

.nav-item.active {
  color: #000;
  font-weight: 600;
}

.main-content {
  flex: 1;
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: .5rem 6rem 2rem;
}

.filter-controls {
  background: #fff;
  border-radius: 15px;
  max-width: 1400px;
  margin: 4rem auto 3rem;
  padding: 2rem;
  position: relative;
}

.filter-section {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  display: flex;
}

.filter-group {
  align-items: center;
  display: flex;
}

.filter-group select {
  color: #fff;
  cursor: pointer;
  appearance: none;
  background: #cd5530 url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='%23666666' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6,9 12,15 18,9'%3e%3c/polyline%3e%3c/svg%3e") right .7rem center / .9rem no-repeat;
  border: 2px solid #333;
  border-radius: 8px;
  min-width: 140px;
  padding: .7rem 2.5rem .7rem 1.2rem;
  font-family: inherit;
  font-size: .8rem;
  font-style: italic;
  font-weight: 500;
  transition: all .2s;
  box-shadow: 0 1px 3px #6666661a;
}

.filter-group select:hover {
  background: #ae3712;
  border-color: #000;
  transform: translateY(-1px);
  box-shadow: 0 3px 8px #0003;
}

.clear-filters {
  color: #fff;
  cursor: pointer;
  letter-spacing: .01em;
  background: #333;
  border: 2px solid #000;
  border-radius: 6px;
  padding: .7rem 1.5rem;
  font-family: Cormorant Garamond, serif;
  font-size: .8rem;
  font-weight: 500;
  transition: all .2s;
  box-shadow: 0 2px 4px #0000004d;
}

.clear-filters:hover {
  color: #fff;
  background: #000;
  border-color: #333;
  transform: translateY(-1px);
  box-shadow: 0 3px 6px #0006;
}

.about-page {
  justify-content: center;
  padding: 3rem 0;
  display: flex;
}

.about-content {
  text-align: center;
  max-width: 600px;
}

.about-image-container {
  justify-content: center;
  max-width: 300px;
  margin: 0 auto 2.5rem;
  display: flex;
}

.artist-photo {
  border-radius: 4px;
  width: 100%;
  height: auto;
  box-shadow: 0 4px 12px #0000001a;
}

.about-content p {
  color: #444;
  margin-bottom: 1.5rem;
  font-size: 1.1rem;
  line-height: 1.7;
}

.results-info {
  text-align: center;
  margin-bottom: 2rem;
}

.results-info p {
  color: #666;
  font-size: .95rem;
}

.pottery-grid {
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  margin-bottom: 3rem;
  display: grid;
}

.pottery-card {
  cursor: pointer;
  background: #fff;
  border: 1px solid #f0f0f0;
  border-radius: 8px;
  transition: all .3s;
  overflow: hidden;
  box-shadow: 0 2px 10px #00000014;
}

.pottery-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 25px #00000026;
}

.image-container {
  background: #fff;
  width: 100%;
  height: 360px;
  position: relative;
  overflow: hidden;
}

.image-container img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  transition: transform .3s;
}

.pottery-card:hover .image-container img {
  transform: scale(1.05);
}

.nav-arrow {
  color: #fff;
  cursor: pointer;
  z-index: 2;
  opacity: 0;
  backdrop-filter: blur(4px);
  background: #0009;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  font-size: 16px;
  transition: all .2s;
  display: flex;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.pottery-card:hover .nav-arrow {
  opacity: 1;
}

.nav-arrow:hover {
  background: #000c;
  transform: translateY(-50%)scale(1.1);
}

.nav-arrow-left {
  left: 8px;
}

.nav-arrow-right {
  right: 8px;
}

.image-indicators {
  z-index: 2;
  gap: 6px;
  display: flex;
  position: absolute;
  bottom: 8px;
  left: 50%;
  transform: translateX(-50%);
}

.indicator {
  cursor: pointer;
  background: #ffffff80;
  border: none;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  transition: all .2s;
}

.indicator.active {
  background: #fff;
  transform: scale(1.2);
}

.indicator:hover {
  background: #fffc;
}

.modal-overlay {
  z-index: 1000;
  background: #000c;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  display: flex;
  position: fixed;
  inset: 0;
}

.modal-content {
  background: #fff;
  border-radius: 12px;
  width: 100%;
  max-width: 900px;
  max-height: 90vh;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 20px 60px #0000004d;
}

.modal-close {
  color: #fff;
  cursor: pointer;
  z-index: 10;
  backdrop-filter: blur(4px);
  background: #0009;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 36px;
  font-size: 20px;
  transition: all .2s;
  display: flex;
  position: absolute;
  top: 1rem;
  right: 1rem;
}

.modal-close:hover {
  background: #000c;
  transform: scale(1.1);
}

.modal-image-section {
  background: #f8f9fa;
  flex: 2;
  align-items: center;
  display: flex;
}

.modal-image-container {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 500px;
  padding: 2rem;
  display: flex;
  position: relative;
}

.modal-image-container img {
  object-fit: contain;
  border-radius: 8px;
  max-width: 100%;
  max-height: 100%;
}

.modal-nav-arrow {
  color: #fff;
  cursor: pointer;
  z-index: 5;
  backdrop-filter: blur(4px);
  background: #0009;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  font-size: 20px;
  transition: all .2s;
  display: flex;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.modal-nav-arrow:hover {
  background: #000c;
  transform: translateY(-50%)scale(1.1);
}

.modal-nav-arrow-left {
  left: 16px;
}

.modal-nav-arrow-right {
  right: 16px;
}

.modal-image-indicators {
  z-index: 5;
  gap: 8px;
  display: flex;
  position: absolute;
  bottom: 16px;
  left: 50%;
  transform: translateX(-50%);
}

.modal-indicator {
  cursor: pointer;
  background: #ffffff80;
  border: none;
  border-radius: 50%;
  width: 10px;
  height: 10px;
  transition: all .2s;
}

.modal-indicator.active {
  background: #fff;
  transform: scale(1.2);
}

.modal-indicator:hover {
  background: #fffc;
}

.modal-info-section {
  flex-direction: column;
  flex: 1;
  gap: 1.5rem;
  min-width: 300px;
  padding: 2rem;
  display: flex;
}

.modal-title {
  color: #000;
  letter-spacing: .01em;
  margin: 0;
  font-family: Crimson Text, Georgia, serif;
  font-size: 2rem;
  font-weight: 600;
}

.modal-metadata {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

.modal-metadata-item {
  border-bottom: 1px solid #f0f0f0;
  justify-content: space-between;
  align-items: center;
  padding-bottom: .5rem;
  display: flex;
}

.modal-metadata-item .label {
  color: #666;
  text-transform: uppercase;
  letter-spacing: .02em;
  font-size: .9rem;
  font-weight: 600;
}

.modal-metadata-item .value {
  color: #333;
  font-size: 1rem;
}

.modal-image-counter {
  color: #999;
  text-align: center;
  border-top: 1px solid #f0f0f0;
  margin-top: auto;
  padding-top: 1rem;
  font-size: .9rem;
}

.card-content {
  padding: 1.5rem;
}

.piece-title {
  color: #1a1a1a;
  letter-spacing: .02em;
  margin-bottom: 1rem;
  font-family: Fredoka, cursive;
  font-size: 1.3rem;
  font-weight: 500;
  line-height: 1.4;
}

.metadata {
  flex-wrap: wrap;
  gap: .5rem;
  display: flex;
}

.metadata-item {
  letter-spacing: .01em;
  border-radius: 4px;
  padding: .3rem .7rem;
  font-size: .8rem;
  font-weight: 500;
  display: inline-block;
}

.metadata-item.shape, .metadata-item.clay, .metadata-item.season {
  color: #333;
  background: #e0e0e0;
}

.no-results {
  text-align: center;
  color: #8b7355;
  padding: 4rem 2rem;
}

.no-results p {
  margin-bottom: 1.5rem;
  font-size: 1.2rem;
  font-style: italic;
}

.no-results button {
  color: #fff;
  cursor: pointer;
  letter-spacing: .02em;
  background: linear-gradient(135deg, #8b7355 0%, #a0926f 100%);
  border: none;
  border-radius: 8px;
  padding: 1rem 2rem;
  font-family: inherit;
  font-size: 1rem;
  font-weight: 500;
  transition: all .3s;
}

.no-results button:hover {
  background: linear-gradient(135deg, #7a6449 0%, #8f8160 100%);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px #8b73554d;
}

.footer {
  text-align: center;
  color: #666;
  background: #fff;
  padding: 2rem 6rem;
  font-size: .9rem;
}

@media (width <= 1024px) {
  .pottery-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width <= 768px) {
  .site-title {
    margin-bottom: 1.5rem;
    font-size: 2.5rem;
  }

  .vase-container {
    max-width: 500px;
    padding: 2.5rem 3rem 1.5rem;
  }

  .header {
    padding: 0;
  }

  .navigation {
    padding: 1.2rem 0;
  }

  .nav-item {
    padding: .6rem 1.5rem;
    font-size: 1rem;
  }

  .about-content {
    padding: 0 1rem;
  }

  .about-image-container {
    max-width: 250px;
    margin-bottom: 2rem;
  }

  .modal-overlay {
    padding: 1rem;
  }

  .modal-content {
    flex-direction: column;
    max-height: 95vh;
  }

  .modal-image-section {
    flex: none;
  }

  .modal-image-container {
    height: 300px;
    padding: 1.5rem;
  }

  .modal-info-section {
    gap: 1rem;
    min-width: auto;
    padding: 1.5rem;
  }

  .modal-title {
    font-size: 1.5rem;
  }

  .main-content {
    padding: .5rem 3rem 1rem;
  }

  .footer {
    padding: 2rem 3rem;
  }

  .filter-section {
    flex-direction: column;
    align-items: stretch;
    gap: 1rem;
  }

  .filter-group {
    width: 100%;
  }

  .filter-group select {
    width: 100%;
    min-width: auto;
  }

  .clear-filters {
    width: 100%;
    margin-top: .5rem;
  }

  .pottery-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }

  .filter-controls {
    padding: 1.5rem;
  }
}

@media (width <= 480px) {
  .pottery-grid {
    grid-template-columns: 1fr;
  }

  .site-title {
    font-size: 2rem;
  }

  .vase-container {
    max-width: 400px;
    margin: 1rem auto;
    padding: 2rem 2rem 1rem;
  }

  .header {
    padding: 0;
  }

  .main-content {
    padding: .5rem 1.5rem 1rem;
  }

  .footer {
    padding: 2rem 1.5rem;
  }
}
